import { ModuleWithProviders, NgModule, Optional, SkipSelf } from '@angular/core';
import { OnboardingComponent } from './onboarding.component';
import { OnboardingInterface } from '../interface/OnboardingInterface';
import { OnboardingService } from './onboarding.service';
import { HttpClientModule } from '@angular/common/http';
import { CommonModule } from '@angular/common';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';;
import { CreateProfileTemplateComponent } from './create-profile-template/create-profile-template.component';
import { ProfileListComponent } from './profile-list/profile-list.component';
import { RouterModule, Routes } from '@angular/router';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatButtonModule } from '@angular/material/button';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatIconModule } from '@angular/material/icon';
import { MatSelectModule } from '@angular/material/select';
import { MatChipsModule } from '@angular/material/chips';
import { MatInputModule } from '@angular/material/input';
import { MatGridListModule } from '@angular/material/grid-list';
import { MatRadioModule } from '@angular/material/radio';
import { MatPaginatorModule } from '@angular/material/paginator';
import { CdkTableModule } from '@angular/cdk/table';
import { MatSortModule } from '@angular/material/sort';
import { MatTableModule } from '@angular/material/table';
export const onBoardingRoutes: Routes = [
  {
    path: 'onboarding',
    component: OnboardingComponent,
    children: [
      {
        path: 'template-builder/:id',
        component: CreateProfileTemplateComponent
      },
      {
        path: '',
        component: ProfileListComponent
      },

    ]
  },

  // { path: '', redirectTo: '/profil-list', pathMatch: 'full' },
]
@NgModule({
  declarations: [OnboardingComponent, CreateProfileTemplateComponent, ProfileListComponent],
  imports: [
    FormsModule,
    MatFormFieldModule,
    MatButtonModule,
    CommonModule,
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule,
    ReactiveFormsModule,
    MatCheckboxModule,
    MatIconModule,
    MatSelectModule,
    MatInputModule,
    MatChipsModule,
    MatGridListModule,
    MatRadioModule,
    MatPaginatorModule,
    CdkTableModule,
    MatSortModule,
    MatTableModule,
    RouterModule.forChild(onBoardingRoutes)
  ],
  providers: [OnboardingService],
  exports: [OnboardingComponent, HttpClientModule, CreateProfileTemplateComponent, ProfileListComponent, OnboardingComponent, RouterModule]
})

export class OnboardingModule {
  constructor(@Optional() @SkipSelf() parentModule?: OnboardingModule) {
    if (parentModule) {
      throw new Error(
        'OnboardingModule is already loaded. Import it in the AppModule only');
    }
  }

  public static forRoot(config: OnboardingInterface): ModuleWithProviders<OnboardingModule> {
    return {
      ngModule: OnboardingModule,
      providers: [
        { provide: '__NgxOnboarding__', useValue: config },
        OnboardingService
      ]
    };
  }
}
