import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { OnboardingService } from '../onboarding.service';

@Component({
  selector: 'onboarding-profile-list',
  templateUrl: './profile-list.component.html',
  styleUrls: ['./profile-list.component.scss']
})
export class ProfileListComponent implements OnInit {
  displayedColumns = ['Name', 'vcontacts', 'createdAt', 'updatedAt', 'action'];
  datas: any = [];
  dataSource = new MatTableDataSource<any>([]);
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(public service: OnboardingService, private router: Router) { }

  async ngOnInit() {
    await this.getAllProfils()
  }

  onAddProfil() {
    this.router.navigate(['onboarding/template-builder/0'])
  }

  async onDelete(id) {
    const result = await this.service.deleteProfil(id);
    if (result) {
      await this.getAllProfils()
    }
  }

  async getAllProfils() {
    const profile = await this.service.getAllProfil();
    this.datas = profile
    this.dataSource = new MatTableDataSource<any>(this.datas);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

}
