import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable, EventEmitter } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { OnboardingInterface, OnboardingUser } from '../interface/OnboardingInterface';

@Injectable()
export class OnboardingService {
  private token: BehaviorSubject<string> = new BehaviorSubject<string>(null);
  public authentication: EventEmitter<OnboardingUser> = new EventEmitter<OnboardingUser>();
  private _token = this.token.asObservable();
  private header: HttpHeaders;
  public locale: string;
  private endpoint: string;
  private user: OnboardingUser;
  public lang = new BehaviorSubject('');
  public _auth: boolean;
  public mainPath: string = ""
  constructor(@Inject('__NgxOnboarding__') config: OnboardingInterface, private http: HttpClient) {
    this.buildHeaders();
    this.initInstance(config);
  }


  /**
 * @private
 * Generate Header for backend call
 */
  private buildHeaders() {
    this._token.subscribe((token: string) => {
      const bearer: string = 'Bearer ' + token;
      this.header = new HttpHeaders({
        'Authorization': bearer
      });
    });
  }


  /**
 * @param config
 * @private
 * Initialize Data for User Instance
 */
  private initInstance(config: OnboardingInterface) {
    this._auth = !!config.auth;
    if (config.endpoint) {
      this.endpoint = config.endpoint;
      if (config.auth) {
        this.authentication.subscribe((user: OnboardingUser) => {
          if (!user.lang && config.lang) {
            user.lang = config.lang;
          }
          this.locale = user.lang;
          this.lang.next(this.locale);
          if (user.token) {
            this.token.next(user.token);
          }
          this.user = user;
        });
      } else if (config.lang) {
        this.locale = config.lang;
        this.lang.next(this.locale);
        this.user = {
          lang: config.lang,
          token: config.token
        };
      }
      if (config.token) {
        this.token.next(config.token);
      }
    } else {
      return new Error('Please provide endpoint');
    }
  }

  /**
 * @private
 * Generate Random uniq Id for Konverso Instance
 */
  private guid() {
    let random = () => {
      return Math.floor((1 + Math.random()) * 0x10000)
        .toString(16)
        .substring(1);
    };
    return random() + random() + '-' + random() + '-' + random() + '-' + random() + '-' + random() + random() + random();
  }



  /**
 * Send Profil To backend
 * @Post Profil
 */
  public async createProfil(data: any): Promise<string | any> {
    return new Promise((resolve, reject) => {
      const options: any = {
        headers: this.header
      };
      this.http.post(`${this.endpoint}/profils`, data, options)
        .subscribe((data: any) => {
          resolve(data);
        });
    });
  }

  public async updateProfil(data: any): Promise<string | any> {
    return new Promise((resolve, reject) => {
      const options: any = {
        headers: this.header
      };
      this.http.put(`${this.endpoint}/profils`, data, options)
        .subscribe((data: any) => {
          resolve(data);
        });
    });
  }

  public async deleteProfil(id: any): Promise<string | any> {
    return new Promise((resolve, reject) => {
      const options: any = {
        headers: this.header
      };
      this.http.delete(`${this.endpoint}/profils/${id}`, options)
        .subscribe((data: any) => {
          resolve(data);
        });
    });
  }


  public async getProfil(id: any): Promise<string | any> {
    return new Promise((resolve, reject) => {
      const options: any = {
        headers: this.header
      };
      this.http.get(`${this.endpoint}/profils/${id}`, options)
        .subscribe((data: any) => {
          resolve(data);
        });
    });
  }

  public async getAllProfil(): Promise<string | any> {
    return new Promise((resolve, reject) => {
      const options: any = {
        headers: this.header
      };
      this.http.get(`${this.endpoint}/profils`, options)
        .subscribe((data: any) => {
          resolve(data);
        });
    });
  }
  public async getAllTags(): Promise<string | any> {
    return new Promise((resolve, reject) => {
      const options: any = {
        headers: this.header
      };
      this.http.get(`${this.endpoint}/tags`, options)
        .subscribe((data: any) => {
          resolve(data);
        });
    });
  }


  async asignProfil() {

  }

  async getTags() {

  }


  async getCategory() {

  }
}
