import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ENTER, COMMA, SEMICOLON } from '@angular/cdk/keycodes';
import { MatChipInputEvent } from '@angular/material/chips';
import { OnboardingService } from '../onboarding.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'onboarding-create-profile-template',
  templateUrl: './create-profile-template.component.html',
  styleUrls: ['./create-profile-template.component.scss']
})
export class CreateProfileTemplateComponent implements OnInit {
  form: FormGroup
  validCategory = false
  separatorKeysCodes = [ENTER, COMMA, SEMICOLON];
  currentProfil: any = {}
  tags = []
  id: string = '';
  constructor(private fb: FormBuilder, public service: OnboardingService, private route: ActivatedRoute, private router: Router) { }
  categories: any = [
    {
      name: 'Software',
      suffixEndpoint: 'category/software',
      forms: [
        {
          label: '',
          value: null,
          isMandatory: true,
          type: 'text',
        }
      ]
    },
    {
      name: 'Hardware',
      suffixEndpoint: 'category/hardware',
      forms: [
        {
          label: '',
          value: null,
          isMandatory: true,
          type: 'text',
        }
      ]
    },
    {
      name: 'Network',
      suffixEndpoint: 'category/network',
      forms: [
        {
          label: '',
          value: null,
          isMandatory: true,
          type: 'text',
        }
      ]
    }
  ]
  async ngOnInit() {

    this.form = this.fb.group({
      Name: ['', Validators.required],
      Description: [''],
      Tags: ['', Validators.required],
      Status: [false, Validators.required]
    })
    this.tags = await this.service.getAllTags();
    this.id = this.route.snapshot.paramMap.get('id');
    if (this.id != '0') {
      this.currentProfil = await this.service.getProfil(this.id);
      this.form.patchValue(this.currentProfil);
      this.categories = this.currentProfil.categories;
    }

  }

  onAddNewCategory(e) {
    e.preventDefault()
    this.categories.push({
      name: '',
      forms: [
        {
          label: '',
          value: null,
          isMandatory: true,
          type: 'text',
        }
      ]
    })
  }

  onAddNewField(e, i) {
    e.preventDefault()
    this.categories[i].forms.push(
      {
        label: '',
        value: null,
        isMandatory: true,
        type: 'text',
      }
    )
  }


  onRemoveField(e, categoryIndex, fieldIndex) {
    e.preventDefault()
    this.categories[categoryIndex].forms.splice(fieldIndex, 1)
  }

  onRemoveCategory(e, i) {
    e.preventDefault()
    this.categories.splice(i, 1)
  }

  onChangeCategory() {
    return this.categories.every((cat) => cat.name !== '' && cat.forms.every((field) => field.label !== '' && field.type !== '')
    );
  }

  async onSubmit() {
    if (this.form.valid && this.onChangeCategory()) {
      console.log(this.form.value);
      console.log(this.categories);
      const result = this.id !== '0' ?
        await this.service.updateProfil({ ...this.currentProfil, ... this.form.value, categories: this.categories }) :
        await this.service.createProfil({ ... this.form.value, categories: this.categories });
      if (result) {
        this.router.navigate(['./../'])
      } else {
        console.log('errror......');

      }
    } else {
      console.log('xxxxxxxxxxxxxxxxx', this.form.value);

    }
  }


  onChangeType(type: string, indexCategory: number, indexField) {
    if (type === 'select' || type === 'radio') {
      this.categories[indexCategory].forms[indexField].value = [];
    } else if (type === 'text') {
      this.categories[indexCategory].forms[indexField].value = '';
    } else {
      this.categories[indexCategory].forms[indexField].value = false;
    }
  }

  onKeyPress(event: any, indexCategory, indexField) {
    if (event.key === ';') {
      const exist = this.categories[indexCategory].forms[indexField].value.find(
        (opt) =>
          opt.toLocaleLowerCase() ===
          event.target.value.split(';')[0].toLocaleLowerCase()
      );
      if (!exist) {
        this.categories[indexCategory].forms[indexField].value.push(
          event.target.value.split(';')[0]
        );
        event.target.value = '';
      } else {
        event.target.value = '';
      }
    }
  }

  removeKeyword(keyword: string, indexCategory: number, indexField) {
    this.categories[indexCategory].forms[indexField].value = this.categories[
      indexCategory
    ].forms[indexField].value.filter((option) => option !== keyword);
  }

  addKeywordFromInput(event: MatChipInputEvent, indexCategory: number, indexField) {
    const input = event.input;
    const value = event.value;
    if (value) {
      const exist = this.categories[indexCategory].forms[indexField].value.find(
        (opt) =>
          opt.toLocaleLowerCase() === event.value.toLocaleLowerCase()
      );
      if (!exist) {
        this.categories[indexCategory].forms[indexField].value.push(value.trim());
        input.value = ''; console.log("WWWWWWWWWWWW", event);
      } else {
        input.value = '';
        console.log("AAAAAAAAAAA", event);
      }
    }
  }
}
